<template>
    <div class="upload-resume-page">
      <div class="sidebar">
        <input
          type="file"
          id="resume-upload"
          class="upload-input"
          @change="handleFileUpload"
        />
        <label for="resume-upload" class="upload-btn">Upload Resume</label>
      </div>
      <div class="main-content">
        <h2>Top Matching Jobs</h2>
        <ul>
          <li v-for="job in jobs" :key="job.id">
            <JobComponent :job="job" />
          </li>
        </ul>
      </div>
    </div>
  </template>
  
  
<script>
import JobComponent from '@/components/JobComponent.vue';

export default {
    name: 'UploadResumeView',
    components: {
        JobComponent,
    },
    data() {
        return {
            jobs: [],
            user: {
                firstName: "a",
            }
        }
    },
    methods: {
        async handleFileUpload(event) {
            const file = event.target.files[0];
            if (file) {
                const formData = new FormData();
                formData.append('resume', file);
              try {
                const response = await this.$store.$axiosInstance.post('/resume', formData);
                const data = await response.json();
                this.jobs = data.jobs;
                console.log('File uploaded successfully.');
              } catch (error) {
                console.error('Error uploading file:', error);
              }
            }
        }
    }
};
</script>

<style scoped>
.upload-resume-page {
    display: grid;
    grid-template-columns: 1fr 2fr;
    align-items: center;
}

.main-content {
    border-left: 2px solid #000;
    height: 80vh;
}

.upload-input {
  display: none;
}

.upload-btn {
  background-color: white;
  color: #845aa4;
  border: 2px solid #845aa4;
  padding: 10px 20px; 
  font-size: 14px;
  font-weight: 600;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s;
}

.upload-btn:hover {
  background-color: #845aa4;
  color: white;
}

</style>
