        <h2>{{ job.title }}</h2>
        <p class="subtext"> {{ job.companyName }}</p>
        <p class="subtext"> {{ job.location }}</p>
        <p class="subtext"> {{ job.description }}</p>
    </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
    name: 'JobComponent',
    computed: {
        ...mapState({
            job: state => state.UploadResumeView.job
        })
    }
